const code =
`<template>
  <vui-menu mode="horizontal" :selectedName="selectedName" @select="handleSelect">
    <vui-menu-item name="1">
      <vui-icon type="apps" />Navigation 1
    </vui-menu-item>
    <vui-menu-item name="2" disabled>
      <vui-icon type="mail" />Navigation 2
    </vui-menu-item>
    <vui-submenu name="3">
      <template slot="title">
        <vui-icon type="settings" />Navigation 3 - Submenu
      </template>
      <vui-menu-item-group title="Group 1">
        <vui-menu-item name="3-1">Item 1</vui-menu-item>
        <vui-menu-item name="3-2">Item 2</vui-menu-item>
      </vui-menu-item-group>
      <vui-menu-item-group title="Group 2">
        <vui-menu-item name="3-3">Item 3</vui-menu-item>
        <vui-menu-item name="3-4">Item 4</vui-menu-item>
      </vui-menu-item-group>
    </vui-submenu>
    <vui-menu-item name="4" href="https://cn.vuejs.org/" target="_blank">
      <vui-icon type="links" />Navigation 4 - Link
    </vui-menu-item>
  </vui-menu>
</template>

<script>
  export default {
    data() {
      return {
        selectedName: "1"
      };
    },
    methods: {
      handleSelect(name) {
        this.selectedName = name;
      }
    }
  };
</script>
`;

export default code;


