const code =
`<template>
  <div class="example-menu-switch-state">
    <div>
      <vui-radio-group type="button" v-model="mode">
        <vui-radio value="vertical">Vertical</vui-radio>
        <vui-radio value="inline">Inline</vui-radio>
      </vui-radio-group>
      <vui-radio-group type="button" v-model="theme">
        <vui-radio value="light">Light</vui-radio>
        <vui-radio value="dark">Dark</vui-radio>
      </vui-radio-group>
    </div>
    <vui-menu :mode="mode" :theme="theme" :selectedName="selectedName" @select="handleSelect" style="width: 250px;">
      <vui-submenu name="1">
        <template slot="title">
          <vui-icon type="apps" />
          <span>Navigation 1</span>
        </template>
        <vui-menu-item name="1-1"><span>Item 1-1</span></vui-menu-item>
        <vui-menu-item name="1-2"><span>Item 1-2</span></vui-menu-item>
        <vui-menu-item name="1-3"><span>Item 1-3</span></vui-menu-item>
        <vui-menu-item name="1-4"><span>Item 1-4</span></vui-menu-item>
      </vui-submenu>
      <vui-menu-item name="2">
        <vui-icon type="mail" />
        <span>Navigation 2</span>
      </vui-menu-item>
      <vui-submenu name="3">
        <template slot="title">
          <vui-icon type="settings" />
          <span>Navigation 3</span>
        </template>
        <vui-menu-item-group title="Group 1">
          <vui-menu-item name="3-1"><span>Item 3-1</span></vui-menu-item>
          <vui-menu-item name="3-2"><span>Item 3-2</span></vui-menu-item>
        </vui-menu-item-group>
        <vui-menu-item-group title="Group 2">
          <vui-menu-item name="3-3"><span>Item 3-3</span></vui-menu-item>
          <vui-menu-item name="3-4"><span>Item 3-4</span></vui-menu-item>
        </vui-menu-item-group>
      </vui-submenu>
      <vui-submenu name="4">
        <template slot="title">
          <vui-icon type="links" />
          <span>Navigation 4</span>
        </template>
        <vui-menu-item name="4-1"><span>Item 4-1</span></vui-menu-item>
        <vui-menu-item name="4-2"><span>Item 4-2</span></vui-menu-item>
        <vui-submenu name="4-3">
          <template slot="title">
            <span>Item 4-3</span>
          </template>
          <vui-menu-item name="4-3-1"><span>Item 4-3-1</span></vui-menu-item>
          <vui-menu-item name="4-3-2"><span>Item 4-3-2</span></vui-menu-item>
        </vui-submenu>
      </vui-submenu>
    </vui-menu>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        mode: "inline",
        theme: "dark",
        selectedName: "1-1"
      };
    },
    methods: {
      handleToggleCollapsed() {
        this.collapsed = !this.collapsed;
      },
      handleSelect(name) {
        this.selectedName = name;
      }
    }
  };
</script>

<style>
  .example-menu-switch-state {  }
  .example-menu-switch-state .vui-radio-group { margin-right:20px; }
  .example-menu-switch-state .vui-menu-root { margin-top:20px; }
</style>
`;

export default code;


