<template>
	<Example vertical id="example-menu-inline-collapsed">
		<template slot="source">
			<div class="example-menu-inline-collapsed">
				<vui-button @click="handleToggleCollapsed">
					<vui-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'" />
				</vui-button>
				<vui-menu mode="inline" theme="dark" :collapsed="collapsed" :selectedName="selectedName" @select="handleSelect">
					<vui-submenu name="1">
						<template slot="title">
							<vui-icon type="apps" />
							<span>Navigation 1</span>
						</template>
						<vui-menu-item name="1-1"><span>Item 1-1</span></vui-menu-item>
						<vui-menu-item name="1-2"><span>Item 1-2</span></vui-menu-item>
						<vui-menu-item name="1-3"><span>Item 1-3</span></vui-menu-item>
						<vui-menu-item name="1-4"><span>Item 1-4</span></vui-menu-item>
					</vui-submenu>
					<vui-menu-item name="2">
						<vui-icon type="mail" />
						<span>Navigation 2</span>
					</vui-menu-item>
					<vui-submenu name="3">
						<template slot="title">
							<vui-icon type="settings" />
							<span>Navigation 3</span>
						</template>
						<vui-menu-item-group title="Group 1">
							<vui-menu-item name="3-1"><span>Item 3-1</span></vui-menu-item>
							<vui-menu-item name="3-2"><span>Item 3-2</span></vui-menu-item>
						</vui-menu-item-group>
						<vui-menu-item-group title="Group 2">
							<vui-menu-item name="3-3"><span>Item 3-3</span></vui-menu-item>
							<vui-menu-item name="3-4"><span>Item 3-4</span></vui-menu-item>
						</vui-menu-item-group>
					</vui-submenu>
					<vui-submenu name="4">
						<template slot="title">
							<vui-icon type="links" />
							<span>Navigation 4</span>
						</template>
						<vui-menu-item name="4-1"><span>Item 4-1</span></vui-menu-item>
						<vui-menu-item name="4-2"><span>Item 4-2</span></vui-menu-item>
						<vui-submenu name="4-3">
							<template slot="title">
								<span>Item 4-3</span>
							</template>
							<vui-menu-item name="4-3-1"><span>Item 4-3-1</span></vui-menu-item>
							<vui-menu-item name="4-3-2"><span>Item 4-3-2</span></vui-menu-item>
						</vui-submenu>
					</vui-submenu>
				</vui-menu>
			</div>
		</template>
		<template slot="title">折叠内嵌菜单</template>
		<template slot="describe">
			<p>内嵌菜单可以被折叠或展开。</p>
		</template>
		<template slot="code">{{code}}</template>
	</Example>
</template>

<script>
	import Example from "@/components/example";
	import code from "./code";

	export default {
		components: {
			Example
		},
		data() {
			return {
				code,
				collapsed: false,
				selectedName: "1-1"
			};
		},
		methods: {
			handleToggleCollapsed() {
				this.collapsed = !this.collapsed;
			},
			handleSelect(name) {
				this.selectedName = name;
			}
		}
	};
</script>

<style>
	.example-menu-inline-collapsed { max-width:250px; }
	.example-menu-inline-collapsed .vui-menu-root { margin-top:20px; }
</style>


