const code =
`<template>
  <vui-menu mode="inline" :selectedName="selectedName" @select="handleSelect" style="width: 250px;">
    <vui-submenu name="1">
      <template slot="title">
        <vui-icon type="apps" />Navigation 1
      </template>
      <vui-menu-item name="1-1">Item 1</vui-menu-item>
      <vui-menu-item name="1-2">Item 2</vui-menu-item>
      <vui-menu-item name="1-3">Item 3</vui-menu-item>
      <vui-menu-item name="1-4">Item 4</vui-menu-item>
    </vui-submenu>
    <vui-menu-item name="2">
      <vui-icon type="mail" />Navigation 2
    </vui-menu-item>
    <vui-submenu name="3">
      <template slot="title">
        <vui-icon type="settings" />Navigation 3
      </template>
      <vui-menu-item-group title="Group 1">
        <vui-menu-item name="3-1">Item 1</vui-menu-item>
        <vui-menu-item name="3-2">Item 2</vui-menu-item>
      </vui-menu-item-group>
      <vui-menu-item-group title="Group 2">
        <vui-menu-item name="3-3">Item 3</vui-menu-item>
        <vui-menu-item name="3-4">Item 4</vui-menu-item>
      </vui-menu-item-group>
    </vui-submenu>
    <vui-submenu name="4">
      <template slot="title">
        <vui-icon type="links" />Navigation 4
      </template>
      <vui-menu-item name="4-1">Item 1</vui-menu-item>
      <vui-menu-item name="4-2">Item 2</vui-menu-item>
      <vui-submenu name="4-3">
        <template slot="title">Item 3</template>
        <vui-menu-item name="4-3-1">Item 3-1</vui-menu-item>
        <vui-menu-item name="4-3-2">Item 3-2</vui-menu-item>
      </vui-submenu>
    </vui-submenu>
  </vui-menu>
</template>

<script>
  export default {
    data() {
      return {
        selectedName: "1-1"
      };
    },
    methods: {
      handleSelect(name) {
        this.selectedName = name;
      }
    }
  };
</script>
`;

export default code;


